import React from 'react'
import { HashRouter as Router, Route, Redirect, Switch } from "react-router-dom";
import PageTransition from 'react-router-page-transition';

import CreatorAccount from 'containers/CreatorAccount';
import ConsumerAccount from 'containers/ConsumerAccount';
import EditFieldForm from 'containers/EditFieldForm';


const App = () => {
    return (
        <Router>
          <Route render={({ location }) => (
            <PageTransition timeout={500}>
              <Switch location={location}>
                <Route exact path='/' component={() => (<Redirect to='/creator/account' />)} />
                <Route exact path="/creator/account" component={CreatorAccount} />
                <Route path="/creator/account/handle" component={props => (
                    <EditFieldForm
                        name='handle'
                        pageTitle='Edit Handle'
                        label='Brand Name'
                        initialValue='spotravnyy'
                        {...props}
                    /> 
                )} />
                <Route path="/creator/account/email" component={props => (
                    <EditFieldForm
                        name='email'
                        pageTitle='Edit email'
                        label='Email'
                        initialValue='spotravnyy@lohika.com'
                        {...props}
                    /> 
                )} />

                <Route exact path="/consumer/account" component={ConsumerAccount} />
                <Route path="/consumer/account/handle" component={props => (
                    <EditFieldForm
                        name='handle'
                        pageTitle='Edit Handle'
                        label='Username'
                        initialValue='spotravnyy'
                        {...props}
                    /> 
                )} />
                <Route path="/consumer/account/email" component={props => (
                    <EditFieldForm
                        name='email'
                        pageTitle='Edit email'
                        label='Email'
                        initialValue='spotravnyy@lohika.com'
                        {...props}
                    /> 
                )} />
              </Switch>
            </PageTransition>
          )} />
        </Router>
    )
}

export default App