import React, { useState } from 'react'
import Page from 'components/Page'
import WithFooter from 'components/WithFooter'
import Input from 'components/Input'
import Button from 'components/Button'

const EditFieldForm = ({
    history,
    name,
    pageTitle,
    label,
    initialValue
}) => {
    const [value, setValue] = useState(initialValue)

    const submit = () => {
        if (window.confirm(`Value '${value}' will be saved to the backend. Shall it be successful?`)) {
            history.goBack()
        } else {

        }
    }
    
    return (
        <Page className="lowLevelPage" title={pageTitle} onBackNavigation={() => history.goBack()}>
            <WithFooter
                children={(
                    <div>
                        <Input
                            name={name}
                            label={label}
                            value={value}
                            onChange={setValue}
                        />
                    </div>
                )}
                footer={(
                    <div style={{textAlign: 'center', padding: '27px 0'}}>
                        <Button onClick={submit}>
                            Update
                        </Button>
                    </div>
                )}
            />
        </Page>
    )
}

export default EditFieldForm